.movie_item {
  display: flex;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 10px 10px 20px 5px;
  margin-bottom: 20px;
  gap: 50px;
}

.movie_img {
  width: 190px;
  height: 250px;
}

.movie_img img {
  width: 100%;
  height: 100%;
}

.movie_info {
  display: flex;
  gap: 20px;
  flex-direction: column;
}

.movie_rating {
  background-color: orange;
  color: white;
  text-align: center;
  width: 40px;
  border-radius: 15px;
  padding: 5px;
}
